@font-face {
  font-family: "TTFirsNeue";
  src: url("./fonts/tt_firs_neue/SuisseIntl-Regular.woff2") format("woff2"),
  url("./fonts/tt_firs_neue/SuisseIntl-Regular.woff") format("woff"),
  url("./fonts/tt_firs_neue/SuisseIntl-Regular.ttf") format("truetype");
}

body, html {
  padding: 0;
  margin: 0;
  font-family: "TTFirsNeue";
  background-color: rgba(249, 250, 251, 1);
  overflow-x: hidden;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: normal;
  margin: 0;
}

h1 {
  font-weight: bold;
}

.none-seo {
  display: none;
}

.dropdown-menu {
  display: none;
  position: absolute;
  top: 50px;
  right: 0;
  border-radius: 6px;
  padding-top: 8px;
  padding-bottom: 8px;
  padding-right: 6px;
  padding-left: 6px;
  z-index: 10000;
  background-color: white;
  box-shadow: 0px 1px 16px 0px rgba(0, 0, 0, 0.2);

  ul {
    list-style: none;
    padding: 0;
    margin: 0;

    li {
      display: flex;
      width: 102px;
      height: 33px;
      border-radius: 6px;
      padding-top: 6px;
      padding-right: 16px;
      padding-bottom: 6px;
      text-align: center;
      justify-content: center;
      padding-left: 16px;
      align-items: center;

      a {
        text-decoration: none;
        color: #000;
      }
    }

    li:hover {
      background-color: #ebebeb;
    }
  }
}

.dropdown-menu.active {
  display: block;
}

body {
  .time {
    display: flex;
    align-items: center;
    color: rgba(36, 38, 43, 0.5254901961);

    span {
      margin-left: 5px;
    }
  }

  .tag {
    background-color: rgba(128, 128, 128, 0.1294117647);
    border-radius: 10px;
    color: black;
    padding: 8px 36px;
    font-size: 16px;
  }

  .header {
    display: flex;
    justify-content: space-between;

    align-items: center;
    padding: 30px 58px;
    @media (max-width: 578px) {
      padding: 24px;
    }
    margin-bottom: 24px;
    background-color: #F9FAFB;
    box-shadow: 0px 3.03px 17.7px 0px rgba(0, 0, 0, 0.05);
    border-bottom-left-radius: 22px;
    border-bottom-right-radius: 22px;

    .logo, .logo > svg {
      width: 51px;
      height: 52px;
      @media (max-width: 578px) {
        width: 36px;
      }

    }

    .rightBlock {
      display: flex;
      justify-content: space-between;
      align-items: center;

      .menu {
        ul {
          display: flex;
          list-style: none;
          color: black;

          li {
            color: black;
            cursor: pointer;
            margin: 0 20px;
            font-size: 18px;

            a {
              text-decoration: none;
              text-decoration: initial;
              color: black;
            }
          }
        }
      }

      .search {
        position: relative;
        margin-left: 200px;
        @media (max-width: 682px) {
          margin: 0 auto;
        }

        svg:not(.clear-btn) {
          position: absolute;
          left: 10px;
          top: 12px;
          z-index: 1;
        }

        input {
          padding: 14px 0px;
          border-radius: 12px;
          color: rgba(36, 38, 43, 0.5);
          font-size: 18px;
          font-weight: 400;
          width: 190px;
          padding-left: 54px;
          border: medium none currentColor;
          border: initial;
          background-color: rgba(136, 144, 162, 0.08);
          outline: medium none invert;
          outline: initial;
        }

        input::-moz-placeholder {
          color: rgba(36, 38, 43, 0.5);
          font-size: 18px;
        }

        input::placeholder {
          color: rgba(36, 38, 43, 0.5);
          font-size: 18px;
        }
      }

      .mobile-menu {
        display: none;
      }
    }
  }


  .HelloAbc {
    position: relative;
    width: 100%;
    max-width: 100%;
    overflow: hidden;
    padding: 0 58px;
    border-radius: 20px;
    margin-bottom: 30px;
    box-sizing: border-box;
    @media (max-width: 578px) {
      margin-bottom: 26px;
      padding: 0 24px;
    }

    .gallery {
      position: relative;
      width: 100%;
      max-width: 100%;
      overflow: hidden;
      display: flex;
      flex-direction: column;

      .gallery-track {
        position: relative;
        width: 100%;
        display: flex;
        transition: transform 0.5s cubic-bezier(.4, 0, .2, 1);
        will-change: transform;

        .gallery-slide {
          position: relative;
          min-width: 100%;
          text-align: center;
          box-sizing: border-box;

          img {
            top: 0;
            left: 0;
            width: 100%;
            z-index: -1;
          }

          img.full {
            display: block;
            border-radius: 20px;
          }

          img.mob {
            display: none;
          }

          .text {
            position: absolute;
            width: 100%;
            display: flex;
            flex-direction: column;
            top: 12%;

            h1 {
              font-size: 32px;
              margin-block-start: 0px;
              margin-block-end: 5px;
              color: rgba(255, 255, 255, 0.9);
              z-index: 1;

              @media (max-width: 578px) {
                font-size: 18px;
              }
            }

            span {
              font-size: 22px;
              color: rgba(255, 255, 255, 0.5);
              @media (max-width: 578px) {
                font-size: 16px;
              }
              z-index: 1;

              svg {
                margin-top: 6px;
                margin-left: 8px;
                position: absolute;
              }
            }

            .button {
              margin: 0 auto;
              margin-top: 16px;
              padding: 10px 24px;
              background-color: #FFFFFF;
              color: #1D1E22;
              border: none;
              border-radius: 8px;
              font-size: 18px;
              @media (max-width: 578px) {
                font-size: 12px;
                margin-top: 10px;
                padding: 4px 12px;
                border-radius: 2px;
              }
              cursor: pointer;
              z-index: 1;
              transition: background 0.3s ease;
              text-decoration: unset;

              &:hover {
                background: #3A3D44;
              }
            }
          }
        }
      }

      .gallery-dots {
        z-index: 2;
        display: block;
        margin: 0 auto;
        margin-top: 12px;

        .dot {
          width: 56px;
          height: 6px;
          background: #E3E3E3;
          border-radius: 5px;
          display: inline-block;
          transition: background 0.3s;
          cursor: pointer;

          &.active {
            background: #1D1E2250;
          }
        }
      }
    }

    .inte {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      background-color: rgba(0, 0, 0, 0.7);
      z-index: 3;
      margin: 85px 0px;

      &.hidden {
        display: none;
      }

      h1 {
        font-size: 48px;
        margin-bottom: 56px;
        transform: scaleX(1.2);
        transform-origin: center;
        text-align: center;
        margin-top: 56px;
        margin-block-start: 0px;
        margin-block-end: 0px;
        color: rgba(255, 255, 255, 0.9);
      }

      .tags {
        display: flex;
        overflow: hidden;
        margin-top: 68px;
        justify-content: center;
        flex-wrap: wrap;
        gap: 8px;

        h4 {
          color: white;
          background-color: rgba(71, 76, 87, 1);
          border-radius: 209px;
          padding: 10px 32px;
          font-size: 24px;
          font-weight: 400;
          display: flex;
          justify-content: center;
          align-items: center;

          span {
            display: inline-block;
            white-space: nowrap;
          }

          &.white {
            background-color: white;
            color: rgba(0, 0, 0, 0.9);

            .smile {
              width: 44px;
              margin-left: 8px;
              display: inline-block;
            }
          }
        }
      }
    }
  }

  @media (max-width: 960px) {
    body {
      .HelloAbc {
        padding: 0 15px;

        .gallery {
          .gallery-track {
            .gallery-slide {
              img.full {
