              }

              h1 {
                font-size: 24px;
              }

              span {
                font-size: 18px;
              }
            }
          }
        }
      }

      // ... existing code ...
    }
  }


  .wrapper {
    display: flex;
    padding: 0 58px;
    flex-direction: column;

    .Breadcrumbs {
      display: flex;
      align-items: center;

      a {

        color: grey;
        display: flex;
