  .bigArticleImage {
    width: 100%;
    height: auto;
    border-radius: 28px;
    margin-top: 16px;
  }

  .razmetkaArticle {
    display: flex;
    position: relative;

    .article {
      max-width: calc(100% - 574px);
      margin-right: 90px;
      @media (max-width: 1300px) {
        max-width: 100%;
        margin-right: 0px;
      }

      width: 100%;
    }

    .navigationSlipper {
      @media (max-width: 1300px) {
        display: none;
      }
      width: calc(100% - calc(100% - 574px));
      /*padding-left: 90px;*/
      margin-top: 40px;
      display: flex;
      align-items: start;

      .slipper {
        background: rgba(47, 48, 52, 1);
        color: white;
        font-weight: 400;
        border-radius: 28px;
        padding: 20px 32px;
        font-size: 24px;
        width: 420px;

        span {
          margin-left: 10px;
        }

        ul {
          list-style-type: none;
          counter-reset: list-counter;
          padding-left: 40px;
          line-height: 32px;
          margin-block-end: 0px;
        }

        li {
          counter-increment: list-counter;
          position: relative;

          &:hover {
            color: #828385;
          }
        }

        li::before {
          content: counter(list-counter) ".";
          position: absolute;
          left: -30px;
          width: 20px;
          text-align: right;
        }

        a {
          color: white;
          text-decoration: unset;
        }
      }
    }
  }

  .article {
    display: flex;
    flex-direction: column;

    h1 {
      margin: 40px 0;
      font-size: 42px;
    }

    h2 {
      margin: 30px 0;
      font-size: 36px;
    }

    .article-info {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      margin-bottom: 40px;
    }

    .article-info > * {
      margin-right: 22px;
      color: rgba(36, 38, 43, 0.5254901961);
      font-size: 14px;
    }


    ul {
      list-style-type: none; /* Убираем стандартные маркеры (точки) */
      counter-reset: list-counter; /* Инициализируем счетчик */
      padding-left: 96px; /* Отступ для цифр */
      line-height: 32px;
      margin-block-end: 0px;
    }

    li {
      counter-increment: list-counter; /* Увеличиваем счетчик для каждого <li> */
      position: relative; /* Для позиционирования цифр */
      display: flex;
      align-items: center;
    }

    li::before {
      content: counter(list-counter);
      position: absolute;
      left: -92px;
      width: 20px;
      text-align: center;
      background-color: white;
      padding: 15px 26px;
      border-radius: 20px;
      box-shadow: 0px 11.09px 25.4px 0px rgba(36, 38, 43, 0.07);

    }

    .content {
      display: flex;
      flex-direction: column;
      font-size: 22px;

      p {
        margin: 0px;
      }

      .note {
        box-shadow: 0px 11.09px 25.4px 0px rgba(36, 38, 43, 0.07);
        padding: 22px 34px;
        border-radius: 20px;
      }

      .BlockImages {
        display: flex;
        margin-top: 26px;

        .img {
          width: 100%;

          img {
            width: 100%;
            border-radius: 16px;
          }

          span {
            font-size: 18px;
            color: rgba(36, 38, 43, 0.5254901961);
          }
        }
      }
    }
  }

