      }
    }
  }

  .CatalogFilter {
    margin-bottom: 36px;

    .filter {
      margin-top: 36px;
      display: flex;
      flex-direction: row;
      overflow: auto;
      justify-content: space-between;
      gap: 8px;
    }

    .filter > h4 {
