  .Categories {
    position: relative;
    overflow: hidden;

    padding: 0 80px;

    width: calc(100% - 80px);

    @media (max-width: 578px) {
      padding: 0 24px;
      width: calc(100% - 48px);
      margin-bottom: 24px;
    }

    padding-right: 0px;

    .leftArrow {
      position: absolute;
      z-index: 9;
      top: 214px;
      left: calc(80px - 64px);
      cursor: pointer;

      &:hover {
        opacity: 0.8 !important;
      }
    }

    .rightArrow {
      position: absolute;
      z-index: 9;
      top: 214px;
      right: calc(80px - 64px);
      cursor: pointer;

      &:hover {
        opacity: 0.8 !important;
      }
    }

    .CategoriesBlock {
      display: flex;
      overflow-x: auto;
      scroll-behavior: smooth;
      -webkit-overflow-scrolling: touch;
      scrollbar-width: none; // Firefox
      -ms-overflow-style: none; // IE and Edge
      gap: 0px 20px;

      cursor: pointer;
      margin-top: 24px;
      margin-bottom: 30px;
      position: relative;
      overflow: hidden;
      @media (max-width: 578px) {
        transform: unset;
        margin-bottom: 0px;
      }


      &::-webkit-scrollbar {
        display: none; // Chrome, Safari, Opera
      }

      .cat {
        display: flex;
        flex: 0 0 auto;
        text-decoration: none;
        color: inherit;

        text-decoration: none;
        flex-direction: column;
        align-items: center;

        box-shadow: 0px 11.094px 25.4px 0px rgba(36, 38, 43, 0.07);

        .ico {
          overflow: hidden;

          position: relative;
          display: inline-block;


          img {
            width: 260px;
            height: 378px;
            display: block;

            border-radius: 16px;
          }

          .hover-img {
            position: absolute;
            top: 0;
            left: 0;
            opacity: 0;
            transition: opacity 0.2s ease;
          }

          &:hover {
            .hover-img {
              opacity: 1;
            }
          }
        }

        .title {
          font-weight: 600;
          font-size: 24px;
          color: black;
          bottom: 20px;
          text-align: center;
          position: absolute;
        }


        &:last-child {
          margin-right: 0;
        }
      }
    }

    .leftArrow,
    .rightArrow {
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      cursor: pointer;
      z-index: 1;
      transition: opacity 0.3s ease;

      &:hover {
        opacity: 0.8 !important;
      }
    }

  }

  .divider {
    width: 100%;
    height: 5px;
    background: rgba(234, 235, 236, 0.50);
    margin: 56px 0;
  }

  .Dictionary {
    margin-bottom: 50px;
    position: relative;

    .Abc {
      position: absolute;
      right: 0px;
      top: 0px;
      margin-top: 10px;
    }

    h3 {
      margin-top: 30px;
      font-size: 70px;
      margin-bottom: 0px;
    }

    ul {
      margin-top: 40px;
      padding: 0px;
      list-style: none;

      li {
        margin-bottom: 18px;

        a {
          font-size: 26px;
          color: #949393;
          text-decoration: none;
          text-decoration: initial;
        }
      }
    }
  }

  .AlphabetBlock {
    .switcher {
      display: flex;
      justify-content: center;
      margin-bottom: 24px;

      button {
        width: 108px;
        height: 34px;
        border: 1px solid transparent;
        border-radius: 10px;
        color: #111111;
        font-size: 12px;
        cursor: pointer;
        background-color: rgba(234, 235, 236, 0.3137254902);
        z-index: 1;
      }

      button.active {
        color: white;
        background-color: #111111;
        z-index: 99;
      }
    }

    .DictBlock {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
    }

    .Alphabet {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      border-bottom: 1px solid rgba(234, 235, 236, 0.3137254902);
      padding-bottom: 40px;
      gap: 24px;
      @media (max-width: 402px) {
        gap: 16px;
      }
      justify-content: center;

      div {
        width: 142px;
        height: 122px;
        font-size: 36px;
        @media (max-width: 1210px) {
          font-size: 36px;
          width: 175px;
          height: 160px;
        }
        @media (max-width: 402px) {
          font-size: 22px;
          width: 74px;
          height: 74px;
        }
        border: 1px solid transparent;
        background-color: white;
        box-shadow: 0px 11.32px 19.5px 0px rgba(36, 38, 43, 0.03);
        border-radius: 12px;
        cursor: pointer;
        display: flex;
        justify-content: center;
        align-items: center;
      }

      div:hover, div.active {
        background-color: #24262B;
        color: white;
      }
    }
  }

  .RelatedArticles {
    display: flex;
    flex-direction: column;


    .index {
      display: none;
    }


    .leftArrow {
      position: absolute;
      top: 50%;
      left: 16px;
      cursor: pointer;
      z-index: 22;

      &:hover {
        opacity: 0.8;
      }
    }

    .rightArrow {
      position: absolute;
      top: 50%;
      right: 16px;
      cursor: pointer;
      z-index: 22;

      &:hover {
        opacity: 0.8;
      }
    }

    .AriclesBlock {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      gap: 40px 20px;
      margin-top: 24px;

      a {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        flex-basis: calc(33.33% - 20px);

        justify-content: space-between;
        text-decoration: none;
        text-decoration: initial;

        img {
          width: 100%;
          border-radius: 16px;
          box-shadow: 0px 7.797px 17.851px 0px rgba(36, 38, 43, 0.07);
        }

        h3 {
          font-size: 28px;
          padding-top: 12px;
          padding-bottom: 8px;
          color: black;
        }

        .Descr {
          font-size: 18px;
          color: #24262B;

        }

        .Nav {
          display: flex;
          flex-direction: row;
          align-items: center;
          margin-top: 14px;
          justify-content: space-between;
        }
      }
    }
  }
}

